/**
 * PREMIUM FEATURE: Physical Letter Service (Lob.com)
 *
 * Handles sending physical collection letters via postal mail.
 * UK-ready with proper address formatting and legal templates.
 *
 * Setup Instructions:
 * 1. Sign up for Lob account at https://www.lob.com
 * 2. Verify UK service availability (or use alternative like Stannp, Postary)
 * 3. Get API key from dashboard
 * 4. Add to .env.local:
 *    LOB_API_KEY=your_api_key
 *    LOB_RETURN_ADDRESS_LINE1=Your Business Name
 *    LOB_RETURN_ADDRESS_LINE2=123 Your Street
 *    LOB_RETURN_ADDRESS_CITY=London
 *    LOB_RETURN_ADDRESS_POSTCODE=SW1A 1AA
 * 5. Install package: npm install lob
 *
 * UK Legal Requirements for Debt Collection Letters:
 * - Must include creditor name and address
 * - Must state amount owed and payment terms
 * - Must provide 14 days for response (Consumer Credit Act)
 * - Letter Before Action must warn of legal proceedings
 * - Must comply with FCA debt collection rules
 *
 * Lob UK Pricing (indicative - verify current rates):
 * - Single-page letter: ~£0.80
 * - Multi-page letter: ~£1.20
 * - Tracked delivery: +£1.50
 */

import { logError, logExternalApiCall } from '@/utils/logger';
import Lob from 'lob';

/**
 * UK Address format
 */
export interface UKAddress {
  recipientName: string;
  companyName?: string;
  line1: string;
  line2?: string;
  city: string;
  postcode: string;
  country: 'GB'; // ISO country code
}

/**
 * Letter template types
 */
export type LetterTemplate = 'gentle' | 'final_warning' | 'lba'; // Letter Before Action

/**
 * Initialize Lob client
 */
function getLobClient() {
  const apiKey = process.env.LOB_API_KEY;

  if (!apiKey) {
    throw new Error('Lob API key not configured');
  }

  return new Lob(apiKey);
}

/**
 * Get letter HTML template
 * These templates comply with UK debt collection regulations
 */
function getLetterHTML(
  template: LetterTemplate,
  params: {
    recipientName: string;
    businessName: string;
    businessAddress: string;
    invoiceReference: string;
    amount: number;
    dueDate: string;
    daysPastDue: number;
    invoiceDate: string;
  }
): string {
  const { recipientName, businessName, businessAddress, invoiceReference, amount, dueDate, daysPastDue, invoiceDate } = params;

  const letterDate = new Date().toLocaleDateString('en-GB', {
    day: 'numeric',
    month: 'long',
    year: 'numeric',
  });

  // Base styles for all letters
  const baseStyles = `
    <style>
      body { font-family: Arial, sans-serif; font-size: 12pt; line-height: 1.6; margin: 2cm; }
      .header { margin-bottom: 2cm; }
      .sender { margin-bottom: 1cm; }
      .recipient { margin-bottom: 1cm; }
      .date { margin-bottom: 1cm; }
      .content { margin-bottom: 1cm; }
      .footer { margin-top: 2cm; font-size: 10pt; color: #666; }
      .highlight { font-weight: bold; }
      .warning { font-weight: bold; color: #d32f2f; }
      table { width: 100%; border-collapse: collapse; margin: 1cm 0; }
      th, td { padding: 8px; text-align: left; border-bottom: 1px solid #ddd; }
    </style>
  `;

  if (template === 'gentle') {
    return `
      <!DOCTYPE html>
      <html>
      <head>${baseStyles}</head>
      <body>
        <div class="header">
          <div class="sender">
            ${businessName}<br>
            ${businessAddress.replace(/\n/g, '<br>')}
          </div>
          <div class="date">${letterDate}</div>
          <div class="recipient">
            ${recipientName}
          </div>
        </div>

        <div class="content">
          <p><strong>Re: Outstanding Invoice ${invoiceReference}</strong></p>

          <p>Dear ${recipientName},</p>

          <p>We are writing to remind you of an outstanding payment for services provided. Our records show the following invoice remains unpaid:</p>

          <table>
            <tr>
              <th>Invoice Number</th>
              <th>Invoice Date</th>
              <th>Due Date</th>
              <th>Amount Due</th>
            </tr>
            <tr>
              <td>${invoiceReference}</td>
              <td>${invoiceDate}</td>
              <td>${dueDate}</td>
              <td class="highlight">£${amount.toFixed(2)}</td>
            </tr>
          </table>

          <p>Payment is now <span class="highlight">${daysPastDue} days overdue</span>.</p>

          <p>If you have already made this payment, please disregard this letter and accept our thanks. If you have any queries about this invoice or are experiencing difficulties with payment, please contact us immediately to discuss.</p>

          <p>Otherwise, we kindly request payment of the outstanding amount within <span class="highlight">14 days</span> of the date of this letter.</p>

          <p><strong>Payment can be made by:</strong></p>
          <ul>
            <li>Bank transfer to the account details shown on the original invoice</li>
            <li>Card payment via the secure link provided in your invoice email</li>
          </ul>

          <p>We value your business and look forward to resolving this matter promptly.</p>

          <p>Yours sincerely,</p>
          <p>${businessName}</p>
        </div>

        <div class="footer">
          <p>This letter does not affect your statutory rights. If you are experiencing financial difficulties, please contact us to discuss payment arrangements.</p>
        </div>
      </body>
      </html>
    `;
  }

  if (template === 'final_warning') {
    return `
      <!DOCTYPE html>
      <html>
      <head>${baseStyles}</head>
      <body>
        <div class="header">
          <div class="sender">
            ${businessName}<br>
            ${businessAddress.replace(/\n/g, '<br>')}
          </div>
          <div class="date">${letterDate}</div>
          <div class="recipient">
            ${recipientName}
          </div>
        </div>

        <div class="content">
          <p><strong class="warning">FINAL NOTICE - Outstanding Invoice ${invoiceReference}</strong></p>

          <p>Dear ${recipientName},</p>

          <p class="warning">This is your final notice regarding the outstanding invoice detailed below.</p>

          <table>
            <tr>
              <th>Invoice Number</th>
              <th>Invoice Date</th>
              <th>Due Date</th>
              <th>Amount Due</th>
            </tr>
            <tr>
              <td>${invoiceReference}</td>
              <td>${invoiceDate}</td>
              <td>${dueDate}</td>
              <td class="highlight warning">£${amount.toFixed(2)}</td>
            </tr>
          </table>

          <p>Payment is now <span class="warning">${daysPastDue} days overdue</span>.</p>

          <p>Despite our previous communications, this invoice remains unpaid. We must now insist on immediate payment.</p>

          <p class="warning">Unless payment is received in full within 7 days of the date of this letter, we will be forced to take further action, which may include:</p>
          <ul>
            <li>Referral to a debt collection agency</li>
            <li>Addition of interest and collection costs to the outstanding amount</li>
            <li>Potential legal proceedings</li>
            <li>Reporting to credit reference agencies, which may affect your credit rating</li>
          </ul>

          <p><strong>Payment MUST be made by:</strong></p>
          <ul>
            <li>Bank transfer to the account details shown on the original invoice</li>
            <li>Card payment via the secure link provided in your invoice email</li>
          </ul>

          <p>If there are any genuine disputes regarding this invoice, or if you are experiencing financial difficulties, you must contact us immediately. Failure to respond will result in further action being taken without additional notice.</p>

          <p>Yours sincerely,</p>
          <p>${businessName}</p>
        </div>

        <div class="footer">
          <p><strong>Important:</strong> This is a formal demand for payment. Ignoring this letter may result in legal action and additional costs.</p>
        </div>
      </body>
      </html>
    `;
  }

  // Letter Before Action (LBA) - Legal pre-action notice
  return `
    <!DOCTYPE html>
    <html>
    <head>${baseStyles}</head>
    <body>
      <div class="header">
        <div class="sender">
          ${businessName}<br>
          ${businessAddress.replace(/\n/g, '<br>')}
        </div>
        <div class="date">${letterDate}</div>
        <div class="recipient">
          ${recipientName}
        </div>
      </div>

      <div class="content">
        <p><strong class="warning">LETTER BEFORE ACTION - OUTSTANDING INVOICE ${invoiceReference}</strong></p>
        <p><strong class="warning">PRIVATE AND CONFIDENTIAL</strong></p>

        <p>Dear ${recipientName},</p>

        <p class="warning">This is a formal Letter Before Action pursuant to the Pre-Action Protocol for Debt Claims.</p>

        <p><strong>Outstanding Debt:</strong></p>

        <table>
          <tr>
            <th>Invoice Number</th>
            <th>Invoice Date</th>
            <th>Due Date</th>
            <th>Amount Due</th>
          </tr>
          <tr>
            <td>${invoiceReference}</td>
            <td>${invoiceDate}</td>
            <td>${dueDate}</td>
            <td class="highlight warning">£${amount.toFixed(2)}</td>
          </tr>
        </table>

        <p>Payment is now <span class="warning">${daysPastDue} days overdue</span>.</p>

        <p><strong class="warning">NOTICE OF INTENDED LEGAL PROCEEDINGS</strong></p>

        <p>Despite our previous requests for payment, you have failed to settle this outstanding debt. We hereby give you formal notice that unless payment is received in full within <span class="warning">14 days</span> of the date of this letter, we will commence legal proceedings against you without further notice.</p>

        <p><strong>Should legal proceedings be necessary, you will be liable for:</strong></p>
        <ul>
          <li>The full amount of the outstanding debt (£${amount.toFixed(2)})</li>
          <li>Court fees and legal costs</li>
          <li>Interest at the statutory rate (currently 8% per annum) from the due date</li>
          <li>Any additional costs of enforcement</li>
        </ul>

        <p><strong>If you dispute this debt:</strong></p>
        <p>You must respond to this letter within 14 days setting out full details of your dispute, including any documents that support your position. If you fail to respond, we will assume you do not dispute the debt and will proceed with legal action.</p>

        <p><strong>If you are experiencing financial difficulties:</strong></p>
        <p>You should contact us immediately to discuss payment arrangements. We may be prepared to accept payment by instalments if you can demonstrate genuine financial hardship. However, such arrangements must be agreed before the 14-day deadline expires.</p>

        <p><strong>Advice and Support:</strong></p>
        <p>If you are struggling with debt, free advice is available from:</p>
        <ul>
          <li>Citizens Advice Bureau - www.citizensadvice.org.uk</li>
          <li>StepChange Debt Charity - www.stepchange.org</li>
          <li>National Debtline - 0808 808 4000</li>
        </ul>

        <p class="warning"><strong>THIS IS YOUR FINAL OPPORTUNITY TO RESOLVE THIS MATTER BEFORE LEGAL PROCEEDINGS COMMENCE.</strong></p>

        <p>Payment should be made immediately to the account details shown on the original invoice, or by card payment via the secure link provided.</p>

        <p>Yours sincerely,</p>
        <p>${businessName}</p>
      </div>

      <div class="footer">
        <p><strong>Legal Notice:</strong> This letter is sent in accordance with the Pre-Action Protocol for Debt Claims and constitutes formal notice of our intention to commence legal proceedings. You are strongly advised to seek independent legal advice if you are uncertain about your position.</p>
      </div>
    </body>
    </html>
  `;
}

/**
 * Send physical letter via Lob
 *
 * @param params Letter parameters
 * @returns Letter ID and tracking info
 */
export async function sendCollectionLetter(params: {
  recipient: UKAddress;
  invoiceReference: string;
  amount: number;
  dueDate: string;
  daysPastDue: number;
  invoiceDate: string;
  template: LetterTemplate;
  businessName: string;
  businessAddress: string;
  // Tracking
  invoiceId: string;
  freelancerId: string;
  // Options
  sendCertified?: boolean; // Tracked delivery (extra cost)
}): Promise<{
  success: boolean;
  letterId?: string;
  trackingUrl?: string;
  expectedDeliveryDate?: string;
  error?: string;
}> {
  const startTime = Date.now();

  try {
    // 1. Validate UK postcode
    if (!isValidUKPostcode(params.recipient.postcode)) {
      throw new Error('Invalid UK postcode format');
    }

    // 2. Generate HTML content
    const html = getLetterHTML(params.template, {
      recipientName: params.recipient.recipientName,
      businessName: params.businessName,
      businessAddress: params.businessAddress,
      invoiceReference: params.invoiceReference,
      amount: params.amount,
      dueDate: params.dueDate,
      daysPastDue: params.daysPastDue,
      invoiceDate: params.invoiceDate,
    });

    // 3. Send via Lob
    const client = getLobClient();
    const letter = await client.letters.create({
      description: `Collection letter: ${params.invoiceReference}`,
      to: {
        name: params.recipient.recipientName,
        company: params.recipient.companyName,
        address_line1: params.recipient.line1,
        address_line2: params.recipient.line2,
        address_city: params.recipient.city,
        address_zip: params.recipient.postcode,
        address_country: 'GB',
      },
      from: {
        name: params.businessName,
        address_line1: process.env.LOB_RETURN_ADDRESS_LINE1!,
        address_line2: process.env.LOB_RETURN_ADDRESS_LINE2,
        address_city: process.env.LOB_RETURN_ADDRESS_CITY!,
        address_zip: process.env.LOB_RETURN_ADDRESS_POSTCODE!,
        address_country: 'GB',
      },
      file: html,
      color: false, // Black and white is cheaper
      double_sided: false,
      mail_type: params.sendCertified ? 'certified' : 'first_class',
      metadata: {
        invoiceId: params.invoiceId,
        freelancerId: params.freelancerId,
        invoiceReference: params.invoiceReference,
      },
    });

    const duration = Date.now() - startTime;
    logExternalApiCall('Lob Letters', 'send', duration);

    return {
      success: true,
      letterId: letter.id,
      trackingUrl: letter.url,
      expectedDeliveryDate: letter.expected_delivery_date,
    };

  } catch (error) {
    const duration = Date.now() - startTime;
    logExternalApiCall('Lob Letters', 'send', duration, error as Error);

    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Get letter delivery status
 *
 * @param letterId Lob letter ID
 */
export async function getLetterStatus(letterId: string): Promise<{
  status: 'created' | 'rendered' | 'in_transit' | 'in_local_area' | 'delivered' | 'returned' | 'failed';
  trackingEvents?: Array<{
    date: string;
    event: string;
    location?: string;
  }>;
}> {
  try {
    const client = getLobClient();
    const letter = await client.letters.retrieve(letterId);

    return {
      status: letter.status,
      trackingEvents: letter.tracking_events,
    };
  } catch (error) {
    logError('Failed to fetch letter status', error);
    return {
      status: 'failed',
    };
  }
}

/**
 * Validate UK postcode format
 */
export function isValidUKPostcode(postcode: string): boolean {
  // UK postcode regex (covers all valid formats)
  const postcodeRegex = /^[A-Z]{1,2}\d{1,2}[A-Z]?\s?\d[A-Z]{2}$/i;
  return postcodeRegex.test(postcode.trim());
}

/**
 * Format UK postcode to standard format (AA11 1AA)
 */
export function formatUKPostcode(postcode: string): string {
  const cleaned = postcode.replace(/\s+/g, '').toUpperCase();

  // Insert space before last 3 characters
  if (cleaned.length >= 5) {
    return cleaned.slice(0, -3) + ' ' + cleaned.slice(-3);
  }

  return cleaned;
}

/**
 * Estimate letter cost
 * Prices are indicative - check with Lob for current rates
 */
export function estimateLetterCost(params: {
  pages: number;
  certified?: boolean;
  international?: boolean;
}): number {
  const { pages, certified = false, international = false } = params;

  // Base UK first-class letter
  let cost = 0.80;

  // Additional pages
  if (pages > 1) {
    cost += (pages - 1) * 0.40;
  }

  // Tracked delivery
  if (certified) {
    cost += 1.50;
  }

  // International (if expanded beyond UK)
  if (international) {
    cost += 2.00;
  }

  return cost;
}
